import Vue from 'vue';

import serialService from 'kiwrious-webserial/lib/service/SerialService';
// import {SensorReadResult} from "kiwrious-webserial/lib/data/SensorReadResult";
// import {SensorDecodedValue} from "kiwrious-webserial/lib/data/SensorDecodedValue";

import SensorValueConductivity from '@/components/sensor/value/SensorValueConductivity/index.vue';
import SensorValue from '@/components/sensor/value/SensorValue/index.vue';
import SessionCard from '@/components/Card/index.vue'
import { SENSOR_TYPE } from 'kiwrious-webserial/lib/service/SerialRawValue';
import { COLOR, UNIT } from '../../constants.ts';
import * as moment from 'moment';

let chartData = [
  [],
  []
];

var XAXISRANGE = 6000;
var chartUpdator = null;

export default Vue.extend(
  {
    name: 'Home',
    components:{
      'sensor-value-conductivity': SensorValueConductivity,
      'sensor-value': SensorValue,
      'session-card': SessionCard
    },
    destroyed(){
      if(chartUpdator){
        clearInterval(chartUpdator);
      }
    },
    mounted() {
      let self = this;
      serialService.onSerialData = (decodedData) => {
        self.latestValues = decodedData.decodedValues;
        self.sensor_type = decodedData.sensorType;
        if(!self.color_applied){
          self.applyColor();
        }
      };
      serialService.onSerialConnection = (connect) => {
        self.is_sensor_connected = connect;
        if(!connect){
          self.color_applied = false;
          self.latestValues = null;
        }
      };

      let chartRef = self.$refs.chart;

      chartUpdator = window.setInterval(function () {

        var timeStamp = moment();//Math.floor(Date.now());

        if (self.latestValues && self.is_sensor_connected && self.isRecording) {
          for (var i = 0; i < self.latestValues.length; i++) { // for each observable
            if (self.latestValues[i].type === "number") {
              chartData[i].push({ x: timeStamp, y: self.latestValues[i].value });
            } else {
              chartData[i].push({ x: timeStamp, y: self.latestValues[i].value.value });
            }
          }
        }

        if (self.latestValues && self.is_sensor_connected && self.isRecording){

          let chart_series_data = [];

          for(var j=0; j<self.latestValues.length; j++){
            chart_series_data.push({data: chartData[j], name: self.latestValues[j].label});
            self.series[j].data = chartData[j];
            self.series[j].name = self.latestValues[j].label;
          }

          chartRef.updateSeries(chart_series_data);
 
        }


      }, 1000);

      // window.setInterval(function () {
      //   self.resetData()
      //   chartRef.updateSeries([{data: chart1Data}, {data: chart2Data}], true)
      //   self.series[0].data = chart1Data;
      //   self.series[1].data = chart2Data;
      // }, 60000);

    },
    data() {
      return {
        color_applied: false,
        sensor_type:"",
        is_sensor_connected:false,
        isRecording: false,
        latestValues:null,
        options: {
          colors: [COLOR.DEFAULT,COLOR.DEFAULT],
          chart: {
            foreColor: COLOR.DEFAULT,
            id: 'kiwrious-chart',
            height: 350,
            type: 'line',
            toolbar: {
              show: false
            },
            zoom: {
              enabled: false
            },
            animations: {
              enabled: true,
              easing: 'linear',
              dynamicAnimation: {
                speed: 1100
              }
            },
          },
          stroke: {
            curve: 'smooth',
            width: 3
          },
          legend: {
            labels: {
              colors: [COLOR.DEFAULT]
            }
          },
          yaxis:[
            {
              title: { text: UNIT.DEFAULT, style: { color: COLOR.DEFAULT}},
              axisTicks: {
                color: COLOR.DEFAULT
              },
              axisBorder: {
                color: COLOR.DEFAULT
              },
              labels: {
                style: {
                  colors: [COLOR.DEFAULT]
                }
              }
            },
            {
              opposite: true, 
              title: { text: UNIT.DEFAULT, style: { color: COLOR.DEFAULT}},
              axisTicks: {
                color: COLOR.DEFAULT
              },
              axisBorder: {
                color: COLOR.DEFAULT
              },
              labels: {
                style: {
                  colors: [COLOR.DEFAULT]
                }
              }
            }
          ],
          xaxis: {
            type: 'datetime',
            range: XAXISRANGE,
            labels: {
              format: 'mm:ss'
            },
            axisTicks: {
              color: COLOR.DEFAULT
            },
            axisBorder: {
              color: COLOR.DEFAULT
            }
          },
          grid: {
            borderColor: COLOR.DEFAULT
          },
        },
        series: [
          {
            name: '-',
            data: chartData[0].slice()
          },
          {
            name: '-',
            data: chartData[1].slice()
          }
        ],
        recordings: []
      }

    },
    methods: {
      startChartUpdator(){

      },
      saveRecording(){
        // calculate and add values
        this.recordings.push({
          time:"4.11 PM",
          duration:"42",
          avg:"76",
          min:"72",
          max:"82"
        });
      },
      applyColor(){
        switch(this.sensor_type){
          case SENSOR_TYPE.CONDUCTIVITY:
            this.options.colors[0] = COLOR.CONDUCTIVITY;
            this.updateSingleYAxis(UNIT.CONDUCTIVITY, COLOR.CONDUCTIVITY);
          break;
          case SENSOR_TYPE.VOC:
            this.options.colors[0] = COLOR.VOC;
            this.updateSingleYAxis(UNIT.VOC, COLOR.VOC);
          break;
          case SENSOR_TYPE.HEART_RATE:
            this.options.colors[0] = COLOR.HEART_RATE;
            this.updateSingleYAxis(UNIT.HEART_RATE, COLOR.HEART_RATE);
            break;
          case SENSOR_TYPE.UV:
            this.options.colors[0] = COLOR.LUX;
            this.options.colors[1] = COLOR.UV;
            this.updateDuelYAxis(UNIT.LUX, COLOR.LUX, UNIT.UV, COLOR.UV);
            break;
          case SENSOR_TYPE.HUMIDITY:
            this.options.colors[0] = COLOR.TEMPERATURE;
            this.options.colors[1] = COLOR.HUMIDITY;
            this.updateDuelYAxis(UNIT.TEMPERATURE, COLOR.TEMPERATURE, UNIT.HUMIDITY, COLOR.HUMIDITY);
            break;
          case SENSOR_TYPE.TEMPERATURE:
            this.options.colors[0] = COLOR.AMBIENT_TEMP;
            this.options.colors[1] = COLOR.INFRARED_TEMP;
            this.updateDuelYAxis(UNIT.AMBIENT_TEMP, COLOR.AMBIENT_TEMP, UNIT.INFRARED_TEMP, COLOR.INFRARED_TEMP);
            break;
          case SENSOR_TYPE.TEMPERATURE2:
            this.options.colors[0] = COLOR.AMBIENT_TEMP;
            this.options.colors[1] = COLOR.INFRARED_TEMP;
            this.updateDuelYAxis(UNIT.AMBIENT_TEMP, COLOR.AMBIENT_TEMP, UNIT.INFRARED_TEMP, COLOR.INFRARED_TEMP);
            break;
          default:
            throw new Error(`Unexpected sensor type [${this.sensor_type}]`);
        }
        this.color_applied = true;
      },
      async connectSensor() {
        await serialService.connectAndReadAsync();
      },
      updateSingleYAxis(axisTitle, axisColor){
        this.$refs.chart.updateOptions({
          yaxis: [{
            title: { text: axisTitle, style: { color: axisColor } },
            labels: {
              style: {
                colors: [axisColor]
              }
            }
          }]
        });
      },
      updateDuelYAxis(axisTitle1, axisColor1, axisTitle2, axisColor2){
        this.$refs.chart.updateOptions({
          yaxis: [
            {
              title: { text: axisTitle1, style: { color: axisColor1 } },
              labels: {
                style: {
                  colors: [axisColor1]
                }
              }
            },
            {
              opposite: true,
              title: { text: axisTitle2, style: { color: axisColor2 } },
              labels: {
                style: {
                  colors: [axisColor2]
                }
              }
            }
          ]
        });
      },
      recordValues(){
        this.isRecording = !this.isRecording;
        if(!this.isRecording){ // recording stopped
          if(chartUpdator){
            clearInterval(chartUpdator);
          }
          this.saveRecording();
        }
      },
      exportData(){
        var rows2 = [
          ["time"]
        ];
        rows2[0].push(this.series[0].name);
        if(this.series[1]){
          rows2[0].push(this.series[1].name);
        }
        for (var i = 0; i < chartData[0].length; i++){
          rows2.push([chartData[0][i].x.toDate().getTime(), chartData[0][i].y]);
          if(this.series[1]){
            rows2[i + 1].push(chartData[1][i].y);
          }
        }       
        let csvContent = "data:text/csv;charset=utf-8," + rows2.map(e => e.join(",")).join("\n");
        var encodedUri = encodeURI(csvContent);
        var link = document.createElement("a");
        link.setAttribute("href", encodedUri);
        link.setAttribute("download", "my_data.csv");
        document.body.appendChild(link); // Required for FF

        link.click();
      },
      getNewSeries() {
        
        
      },
      resetData() {
        // Alternatively, you can also reset the data at certain intervals to prevent creating a huge series 
        chartData[0] = chartData[0].slice(chartData[0].length - 10, chartData[0].length);
        chartData[1] = chartData[1].slice(chartData[1].length - 10, chartData[1].length);
      },
      
    },
    computed: {
      recording_button_text(){
        if (this.isRecording){
          return "Stop";
        } else {
          return "Record";
        }
      }
    }
  }
);


